body#tinymce {
  background-color: #fff;
  margin: 12px !important;
}

.g-section-subtitle {
  color: #86AB3C;
  font-family: "Source Sans 3", sans-serif;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
@media print, screen and (min-width: 40em) {
  .g-section-subtitle {
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    line-height: 1.9375rem; /* 129.167% */
  }
}
.g-section-title {
  color: #002B49;
  display: block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 2.75rem;
  font-style: normal;
  font-weight: 600;
  line-height: 1.25;
  letter-spacing: -0.125rem;
}
@media print, screen and (min-width: 40em) {
  .g-section-title {
    font-size: 36px;
  }
}
@media screen and (min-width: 80em) {
  .g-section-title {
    font-size: 46px;
  }
}
.g-section-narrow-p {
  display: block;
  max-width: 36.5rem;
  width: 100%;
  margin: auto;
}
.g-section-xnarrow-p {
  display: block;
  width: 22.5rem;
  max-width: 100%;
  margin: auto;
}
.g-section-intro {
  color: #002B49;
  display: block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin: 0 0 0;
}
.g-small-text {
  font-size: 0.9375rem;
}
.g-medium-text {
  font-size: 1.375rem;
}
.g-large-text {
  font-size: clamp(1.75rem, 3vw, 2.125rem);
  line-height: 3.125rem;
  letter-spacing: clamp(-0.125rem, -0.25vw, -0.125rem);
}
.g-xlarge-text {
  font-size: 7.5rem;
  font-size: clamp(5.25rem, 12vw, 7.5rem);
  font-style: normal;
  font-weight: 750;
  line-height: 0.9;
  letter-spacing: 0.0625rem;
  text-transform: uppercase;
  margin-left: auto;
}
.g-button.button {
  margin-right: 10px;
  background-color: transparent;
  border-radius: 2px;
  border: 1px solid #002B49;
  color: #002B49;
  display: inline-block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 14px !important;
  font-weight: 600;
  margin-top: 1rem;
  letter-spacing: 1.5px;
  padding: 8px 34px 8px 20px;
  position: relative;
  text-transform: uppercase;
}
.g-button.button a {
  position: relative;
}
.g-button.button:before {
  font-family: "Font Awesome 6 Pro";
  content: "\f054";
  font-weight: 400;
  position: absolute;
  top: 8px;
  right: 14px;
  height: 30px;
  font-size: 14px;
  width: 8px;
}
.g-button.button i {
  margin-left: 14px;
}
.g-button.button:hover, .g-button.button.active {
  background-color: rgb(0, 21.9712328767, 37.3);
  color: #fff;
}
.g-image--top-left {
  border-radius: 1rem;
  position: relative;
  top: 0;
  left: -16%;
  display: block;
  transition: all 0.5s ease-in-out;
  padding-bottom: 2rem;
}
@media print, screen and (min-width: 40em) {
  .g-image--top-left {
    left: -6rem;
  }
}
@media screen and (min-width: 80em) {
  .g-image--top-left {
    position: absolute;
    top: 0;
    padding-bottom: 0;
  }
}
@media screen and (min-width: 90em) {
  .g-image--top-left {
    position: absolute;
    top: 0;
  }
}
.g-image--top-right, .g-image--bottom-right {
  position: relative;
  border-radius: 1rem;
  bottom: 0;
  right: -16%;
  margin-left: auto;
  transition: all 0.5s ease-in-out;
  display: block;
  padding-top: 2rem;
}
@media print, screen and (min-width: 40em) {
  .g-image--top-right, .g-image--bottom-right {
    right: -6rem;
  }
}
@media screen and (min-width: 80em) {
  .g-image--top-right, .g-image--bottom-right {
    position: absolute;
    bottom: 4rem;
    padding-top: 0;
  }
}
@media screen and (min-width: 90em) {
  .g-image--top-right, .g-image--bottom-right {
    position: absolute;
  }
}

.button,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button,
.wpfFilterButton.wpfButton {
  background-color: #86AB3C;
  border-radius: 0.3125rem;
  border: none;
  color: #ffffff;
  font-family: "Source Sans 3", sans-serif;
  margin: 1rem 0 0 0;
  min-height: 24px;
  padding: 0.69rem 1.5rem;
  text-align: center;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.75rem;
}
.button i,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button i,
.wpfFilterButton.wpfButton i {
  margin-left: 11px;
}
.button:hover, .button.active,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button:hover,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.active,
.wpfFilterButton.wpfButton:hover,
.wpfFilterButton.wpfButton.active {
  background-color: rgb(0, 21.9712328767, 37.3);
  color: #fff !important;
}
.button.grey,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.grey,
.wpfFilterButton.wpfButton.grey {
  background-color: #f3f3f3;
  color: #86AB3C !important;
}
.button.grey:hover, .button.grey.active,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.grey:hover,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.grey.active,
.wpfFilterButton.wpfButton.grey:hover,
.wpfFilterButton.wpfButton.grey.active {
  background-color: #86AB3C;
  color: #fff !important;
}
.button.button-arrow,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.button-arrow,
.wpfFilterButton.wpfButton.button-arrow {
  padding-right: 40px;
  position: relative;
}
.button.button-arrow:after,
.woocommerce:where(body:not(.woocommerce-block-theme-has-button-styles)) a.button.button-arrow:after,
.wpfFilterButton.wpfButton.button-arrow:after {
  color: #fff;
  content: "\f178";
  font-family: "Font Awesome 6 Pro";
  font-weight: 300;
  position: absolute;
  right: 15px;
  top: 7.5px;
}

.gform_wrapper.gravity-theme.uavionix_wrapper .gform_button {
  padding-right: 28px;
  margin-left: auto;
  position: relative;
}
.gform_wrapper.gravity-theme.uavionix_wrapper .gform_button:before {
  font-family: "Font Awesome 6 Pro";
  content: "\f054";
  font-weight: 700;
  position: absolute;
  top: 5px;
  right: 14px;
  height: 30px;
  font-size: 16px;
  width: 8px;
}